html,
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.wrapper {
    max-width: 420px;
    min-height: 96vh;
    margin: 0 auto;
  }

  .show-input {
    grid-area: total;
  }

  .modifiers {
    grid-area: modif;
  }

  .show-input {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 2rem;
  }

  .modifiers {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }

  .wrapper,
.subgrid {
  display: grid;
}
.wrapper {
    grid-template-areas:
      "total total total total"
      "modif modif modif oper"
      "digit digit digit oper"
      "digit digit digit oper"
      "digit digit digit oper"
      "digit digit digit oper";
    grid-auto-columns: 1fr;
  }